import React, { useState, useEffect } from "react";
import Chat from '../components/Site/Chat';
import Header from '../components/Site/Header';
import Table from '../components/General/Table';
import Game from '../components/Site/Game';

import io from 'socket.io-client'
const socket = io('', { reconnection: true, reconnectionAttempts: 10 });

const Home = () => {

  const [messages, $messages] = useState([]);
  const [players, $players] = useState([]);
  const [value, $value] = useState('Loading ...');
  const [countUsers, $countUsers] = useState('...');
  const [amountBTC, $amountBTC] = useState('...');
  const [amountETH, $amountETH] = useState('...');
  const [graphData, $graphData] = useState({});
  let [results, $results] = useState([]);

  const playersTable = {
    head: ['User', '@', 'Bet', 'Profit'],
    body: players
  }

  useEffect(() => {
    socket.on('connect', () => {
      console.log('connecting ...');
    });
  
    socket.on('getMessages', (socket) => {
      $messages(socket);
    })
  
    socket.on('connectionData', (data) => {
      const { messages, results } = data;

      console.log(data);
  
      $messages(messages);
      $results(results);
    })
  
    socket.on('gameStep', data => {
      let arr = data.players;
  
      let myVal = data.value;
      if (data.gameStatus === 'Game') {
        myVal = `${Number(myVal).toFixed(2)}x`;
  
        let btc = 0;
        let eth = 0;
  
        for (let player of arr) {
          let parts_amount = String(player[2]).split(' ');
          if (parts_amount.length > 1) {
            let currency = parts_amount[1];
            let sum = parts_amount[0];
  
            (currency === 'BTC') ? btc += Number(sum) : eth += Number(sum);
          }
        }
  
        $amountBTC(Number(btc).toFixed(2));
        $amountETH(Number(eth).toFixed(2));
        $countUsers(arr.length);
  
      } else {
        $amountBTC('...');
        $amountETH('...');
        $countUsers('...');
      }
  
      if (data.gameStatus === 'Pause') myVal = `Next Game in ${myVal / 1000} s`;
      if (data.gameStatus == 'Results') {
        if (data.results) {
          $results([...data.results]);
        }
      }
  
      $graphData(data);
  
      $value(myVal);
      $players(arr);
    });
  }, [])

  const sendMessage = (message) => {
  
    // Если нет текста, не отправлять сообщение
    if (message.text) {
      socket.emit('sendMessage', message)
    }

  }

  return (
    <div className="main">
      <Header />
      <div className="content">
        <div className="content_left">
          <div className="block_game">
            <div className="block_game__game">
              <div className="game__graph">
                <Game data={ graphData } value={ value } />
              </div>
              <div className="game__results">
                {
                  results.map((el) => (
                    <div className="item_result color-red" key={ Math.random().toString(36).substring(3) }>{ Number(el.kef).toFixed(2) }</div>
                  ))
                }

              </div>
            </div>
            <div className="block_game__bet">
              <div className="game_bet__row">
                <button>Login to play</button>
              </div>
              <div className="game_bet__row">
                or <a href="/register">register</a>
              </div>
            </div>
          </div>
          <Chat onSendMessage={ sendMessage } messages={ messages } />
        </div>
        <div className="content_right">
          <div className="block_players">
            <div className="players_body scrollStyle">
              <Table data={ playersTable } />
            </div>
            <div className="players_footer">
              <div className="players_footer__item">
                <p>Players: <span>{ countUsers }</span></p>
              </div>
              <div className="players_footer__item">
                <p>Betting: <span>{ amountETH }</span> ETH and <span>{ amountBTC }</span> BTC</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Home